@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    min-height: 40px;
    max-height: 300px;
    margin: 10px;
}

.social{
    max-width:100%;
    display: grid;
}

.social-menu{
    display: flex;
}

.header-div {
    max-width: 100%;
    display: flex;

}

.logo-core{
    max-width: 400px;
    margin-left: 25px;
    margin-top: 20px;
}



.header-link {
    color: #fff;
    font-size: 28px;
    max-width: 100px;
    padding:20px;
    font-family: bold;

}

.links-div{
    max-width: 100%;
    margin-right: 20px;
    margin-top: 20px;
}

   
.main-dad{
    max-width: 100vh;
    height:100vh;
    background-color:rgb(0, 0, 0);
    display:grid;
    grid-template-columns: 45% 55%;
    grid-template-rows: 20% 20% 20% 20% 20%;
    flex-direction:column;

}

.main-title{
    grid-column: 1;
    grid-row: 1;
    padding-left: 100px;
    padding-top: 150px;
    max-width:250px;
    min-width: 150px;

}

.main-title {
    font-family: Montserrat;
    font-size: 1.5em;
    color:white;
    max-width:30vw;
    margin:auto;
}

.miniurl-images{
    grid-column: 1;
    grid-row: 4;
    display:inline-flex;
    margin:auto;
    object-fit: cover;
    border:none;
    padding-left: 100px;
}

.miniurl-images-buttton {
    background-color:#000;
    border:none;
    margin: auto;
}
.banner{
    grid-column: 2;
    display:flex;
    justify-content: center;
    align-items: center;
    margin:auto;
    height:80vh
}
.image-finish{
  max-width: 150%;
  border-radius: 15px;
  margin-left: 130%;  
  margin-top: 100px;
}


.imagem-api{
    width:100%;
    border: 1px solid #000;
}

.atv{
    width:100%;
    border: 3px solid #fff;
    object-fit: cover;
}
.button-main{
    grid-column: 1;
    grid-row: 3;
    width:15vw;
    border-radius: 25px;
    height:40px;
    margin-left: 100px;
    margin-top: 130px;
}

.button-main:hover{
    background-color:rgb(165, 165, 165);
    transition:ease-in 1s;

}
.title-button{
    font-family: bold;
    font-size:20px;
    justify-content:center;
    margin: auto
}


.main {
  display: flex;
  flex-direction: column;
  align-items: inherit;
  padding: 0px;
  margin: 0px;
  background-color:#000;
}
.footer-section{
    background: #fff;
    display: flex;
    flex-direction: row;
    max-width: 100%;
    height: 250px

}

footer{
    display: flex;
    max-width: 100%;
    margin-left: 5%;
    max-height: 400px;
}

.marca{
    display: grid;
    margin-top: 40px;
    max-width: 350px;
    max-height: 40px;
}


.logo-core-black{
    max-width:150px
}

.logo-text{
    font-size: smaller;
    padding-top: 10px;
    width: 250px;
    padding-bottom: 10px;
}

.social-links{
    display: flex; 
    position: absolute; 
    margin-top:12px
}

.link-rede{
    width: 27px;
}


.icon{
    max-width: 20px;
    margin-top: 100px;
    padding: 5px;
}



.lista-footer{
    display: grid;
    position: relative;
    width: 150px;
    font-size: medium;
    margin-top: 40px;
    max-height: 100px;
}

.item-lista{
    color: black;
    height: 25px;
    font-family: bold;
    font-size: 20px

}

hr{
    border-color:#aaa;
    box-sizing:border-box;
    width:100%; 
    background:black;
    height:0.1rem
}

.container-endereco{
    display: flex;
    padding-left: 100px;
    padding-right: 10px;
    background: rgba(209, 201, 201, 0.686);
    margin-left: 100px;
    max-width: 100%;
}

.Card{
    max-width: 250px;
    font-size: small;
    margin-top: 30px;
    padding-left: 40px;
    padding-right: 40px;
}
